import { BrowserRouter, Route, Routes } from 'react-router-dom';
import Exhibition from './pages/Exhibition';
import Main from './pages/Main';
import Header from './components/Header';
import { gitUrl } from './styles/styles';
import Goods from './pages/Goods';
import Community from './pages/Community';

export default function Router() {
  return (
    <BrowserRouter>
      <Header />
      <Routes>
        <Route exact path={`${gitUrl}`} element={<Main />} />
        <Route exact path={`${gitUrl}/exhibition`} element={<Exhibition />} />
        <Route exact path={`${gitUrl}/goods`} element={<Goods />} />
        <Route exact path={`${gitUrl}/community`} element={<Community />} />
      </Routes>
    </BrowserRouter>
  );
}
